import { NextResponse } from "next/server";
import type { NexflowDiscoveredData } from "@/lib/nexflow-types";
import discoveredData from "../../../../../data/nexflow-discovered.json";

export async function GET(req: Request) {
  try {
    const data = discoveredData as NexflowDiscoveredData;

    // Optional query param filtering
    const { searchParams } = new URL(req.url);
    const domain = searchParams.get("domain");
    const network = searchParams.get("network");
    const category = searchParams.get("category");

    let filteredActions = data.actions;

    if (domain) {
      filteredActions = filteredActions.filter((a) => a.domain === domain);
    }
    if (network) {
      filteredActions = filteredActions.filter(
        (a) => a.pricing?.network === network
      );
    }
    if (category) {
      filteredActions = filteredActions.filter((a) => a.category === category);
    }

    return NextResponse.json({
      crawledAt: data.crawledAt,
      actions: filteredActions,
    });
  } catch (error) {
    console.error("Failed to load actions:", error);
    return NextResponse.json(
      { error: "Failed to load actions" },
      { status: 500 }
    );
  }
}

